import React from 'react';
import {
  // BrowserRouter as Router,
  HashRouter as Router,
  Switch,
  Redirect,
} from 'react-router-dom';
import Private from './Private';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import PageStaff from './components/PageStaff';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';

const theme = createMuiTheme({
  palette: {
    background: {
      default: '#fff'
    },
  },
  overrides: {}
});

export default function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <MuiThemeProvider theme={ theme }>
        <CssBaseline />
        <Switch>
          <Redirect exact from={`${process.env.PUBLIC_URL}/`} to={`${process.env.PUBLIC_URL}/staff`} />
          <Private path={`${process.env.PUBLIC_URL}/staff`} component={ PageStaff } />
          <Private path={`${process.env.PUBLIC_URL}/add`} component={ () => <Box component="h1">Отчеты</Box> } />
          <Private path="" component={ () => <Box component="h1">404</Box> } />
        </Switch>
      </MuiThemeProvider>
    </Router>
  );
}